.article{display:flex;flex-flow:row nowrap;position:relative;background:#3b3b3b;box-shadow:1px 1px 5px black;padding:15px}.col-2-30{flex:0 1 30%}.col-2-70{flex:0 1 70%}.avatar{margin-right:15px;border-right:2px solid #c8e08a;display:flex;flex-flow:column nowrap;justify-content:space-between;align-items:center;text-align:center}.avatar img{width:80px;height:80px;border-radius:50%;object-fit:cover;border:2px solid #c8e08a}.avatar span{color:#c8e08a;margin:10px 0;display:block;font-weight:400;font-size:2rem}.story{display:flex;flex-flow:column nowrap}.story .error-message{color:#ff2121;font-size:1.2rem}.story .title h2{font-size:2rem;color:#c8e08a;margin-bottom:10px}.story .title .input{border:2px solid #c8e08a;border-radius:6px;font-family:"Lato",sans-serif;font-size:1.4rem;font-weight:400;background:#f0f0f0;color:#3b3b3b;padding:5px;min-width:100%;margin-bottom:5px}.story .message{color:#fff;font-size:1.4rem;font-weight:400;line-height:1.8rem;white-space:pre-wrap}.story .message .textarea{margin-top:10px;margin-bottom:5px;border:2px solid #c8e08a;border-radius:6px;font-family:"Lato",sans-serif;font-weight:400;font-size:1.4rem;background:#f0f0f0;color:#3b3b3b;padding:5px;min-width:100%;min-height:100px}.story .article-footer{margin-top:10px;text-align:right}.story .bt{margin-top:10px;background:#3b3b3b;color:#c8e08a;padding:5px 10px;outline:none;border:2px solid #c8e08a;font-size:1.2rem;font-family:"Lato",sans-serif;font-weight:400;transition:all 0.3s linear}.story .bt:hover{transition:all 0.3s linear;background:#c8e08a;color:#3b3b3b}.story .bt-left{border-radius:6px 0px 0px 6px;border-right:none}.story .bt-right{border-radius:0px 6px 6px 0px}.story .bt-active{transition:all 0.3s linear;background:#c8e08a;color:#3b3b3b}.story .error-border{border:2px solid #ff2121 !important}.meta{color:#fff;font-style:italic;font-weight:300;font-size:1.1rem}.delete-confirm{position:absolute;display:flex;flex-flow:column nowrap;justify-content:center;width:100%;height:100%;top:0;left: